<script>

/**
 * Nav-bar Component
 */

export default {
  name: "main-facility-status-item",
  props: {
    title: {
      type: String,
      required: true,
    },
    id: {
      type: String, 
      required: true
    },
    items: Array,
    needNarrow: {
      type: Boolean,
      default: true
    },
    alarmLevel: {
      type: String,
      default: "muted"
    },
    selected: {
      type: Boolean,
      default: false
    },
    lastItem: {
      type: Boolean,
      default: false
    },
  },
  computed: {
    tooltipId() {
      return  (mainId, index) => {
        return `tt_${mainId}_${index}`
      }
    },
    flashAlarm() {
      return (this.alarmLevel ==="danger" || this.alarmLevel ==="warning")
    },
    flashAlarm2() {
      let ret = (this.alarmLevel ==="danger")
      console.log('ret'+ret)
      return (this.alarmLevel ==="danger")
    },
    badgeClass() {
      return 'font-weight-bold text-'+(this.alarmLevel ==="danger" || this.alarmLevel ==="warning"?this.alarmLevel:'primary')
      +(this.alarmLevel ==="danger" || this.alarmLevel ==="warning"?' blink-me':'')
    }
  },
  methods: {
  },
  mounted() {
    console.log(this.lastItem);
  }
};
</script>

<template>
  <div :class="'row sensor-item '+(lastItem?' ':' sensor-item-div')+(flashAlarm?' warning-row':'')+(selected?' row-selected':'')">
    <div class="col-lg-3">
      <div valign="center">
        <div :class="(needNarrow?' ':'mb-3')+' sensor-item-title'">
          {{title}}
        </div>
      </div>
    </div>
    <div :class="(items.length<3)?'col-lg-4':'col-lg-3'" v-for="(item, index) in items" :key="index">
      <div class="text-muted font-size-11 row sensor-item-row">
        <div :class="(needNarrow?' ':'mb-3')+' sensor-item-text'" :id="tooltipId(id,index)">
          <div class="sensor-item-unit-top">{{ item.title }}</div>
          
          <div>
            <span :class="badgeClass"
            >{{ item.value}}
            <i :class="flashAlarm2?'mdi mdi-alert font-size-14':''"></i>
            </span>
          </div>
        </div>
        <b-tooltip :target="tooltipId(id,index)" v-if="item.tooltip">{{ item.tooltip }}</b-tooltip>
      </div>
    </div>
  </div>
</template>
