<script>
import { sensorEventList } from './sensor-event-list.js'

export default {
  props: {
    plantNo:{
      type: Number,
      required: true
    }
  },
  data() {
    return {
      sensorEventList: sensorEventList
    }
  }
}
</script>
<template>
<div class="card-box border border-warning">
  <h4 class="header-title mt-0 mb-3">{{ plantNo }}호기 이벤트발생 내역</h4>
  <div class="col-sm-12 col-xl-12 ">
      <div class="timeline" dir="ltr" v-for="( item, index ) in sensorEventList" :key="index">
          <article class="timeline-item">
              <div class="time-show first">
                  <a href="#" class="btn btn-primary width-lg">{{ item.workdate }}</a>
              </div>
          </article>
          <article class="timeline-item" v-for="( subItem, subIndex ) in item.eventList" :key="subIndex">
              <div class="timeline-desk">
                  <div class="panel">
                      <div class="panel-body">
                          <span class="arrow-alt"></span>
                          <span :class="'timeline-icon bg-'+subItem.level"><i class="mdi mdi-circle"></i></span>
                          <p class="timeline-date text-muted"><small> {{ subItem.eventTime }} </small></p>
                          <p :class="'text-'+subItem.level">{{ subItem.event }}</p>
                      </div>
                  </div>
              </div>
          </article>
      </div>
  </div>

</div>  
</template>
