<script>

/**
 * Nav-bar Component
 */

export default {
  name: "main-iot-status-item",
  props: {
    iotData: {
      type: Object,
      requred: true,

    },
    needNarrow: {
      type: Boolean,
      default: true
    },
    tag: {
      type: String,
      required: true,
    },
    alarmLevel: {
      type: String,
      default: "muted"
    },
    toggleMode: {
      type: String,
      default: ""
    },
    selectedItemIndex: {
      type: Number,
      required: true
    },
    selectedItem: {
      type: String,
      default: ""
    }
  },
  // data() {
  //   return {
  //     selectedIndex: -1,
  //   }
  // },
  computed: {
    tooltipId() {
      return  (mainId, index) => {
        return `tt_${this.tag}_${mainId}_${index}`
      }
    },
    flashAlarm() {
      return (item) => {
        return (item.alarmLevel ==="danger" || item.alarmLevel ==="warning")
      }
    },
    flashAlarm2() {
      return (item) => {
        let ret = (item.alarmLevel ==="danger")
        console.log('ret'+ret)
        return (item.alarmLevel ==="danger")
      }
    },
    badgeClass() {
      return (item)=> {
        return 'font-weight-bold text-'+(item.alarmLevel ==="danger" || item.alarmLevel ==="warning"? item.alarmLevel:'primary')
        +(item.alarmLevel ==="danger" || item.alarmLevel ==="warning"?' blink-me':'')
      }
    },
    selected() {
      return (curIndex) => {
        return (this.toggleMode === `sensor-${this.tag}` && this.selectedItemIndex == curIndex)
      }
    }
  },
  methods: {
    doToggleClass(selectedId, item) {
      this.$emit('onToggleClass', `sensor-${this.tag}`, selectedId, item)
    },
  },
  mounted() {
    
  }
};
</script>

<template>
  <table class="table table-hover sensor-widget mb-0">
    <thead>
    <tr class="table-sensor-header">
        <th class="w-25"></th>
        <th class="w-15">열(℃)</th>
        <th class="w-15">연기</th>
        <th class="w-15">온/습도(℃)</th>
        <th class="w-15">CO</th>
        <th class="w-15">불꽃</th>
    </tr>
    </thead>
    <tbody>

      <tr style="cursor:pointer" v-for="(item, index) in iotData.items" :key="index"
      @click="doToggleClass(index, item.item)"
      :class="(flashAlarm(item)?' warning-row':'')+(selected(index)?' row-selected':'') +(needNarrow?' ':' mb-3')"
      >
        <th class="facility-item w-25">
            {{ item.item }}
        </th>
        <td class="w-15" v-for="(checkPoint, subIndex) in item.checkPoints" :key="subIndex">
            <div :class="badgeClass(item)" :id="tooltipId(index,subIndex)">
                <div>{{ checkPoint.value }}
                  <i :class="flashAlarm2(item)?'mdi mdi-alert font-size-14':''"></i>
                </div>
            </div>
            <b-tooltip :target="tooltipId(index,subIndex)" v-if="checkPoint.tooltip">{{ checkPoint.tooltip }}</b-tooltip>
        </td>
      </tr>
    </tbody>
  </table>
</template>
