<script>
import Layout from "@/layouts/main";
import appConfig from "@/app.config";
import PageHeader from "@/components/page-header";
import MainFacilityStatusItem from "./main-facility-status-item.vue";
import MainIotStatusItem from "./main-iot-status-item.vue";
import SensorEventTimeLine from "./sensor-event-timeline.vue";
import { facilTurbin, facilBoiler, facilStorage } from "./data-facil";
import { iotTurbin, iotBoiler, iotStorage } from "./data-iot";
import FacilityStatusChart from "./facility-status-chart.vue";
import { columnChart } from "./columnchart-data";
import SensorDetails from "./sensor-details/sensor-details.vue";

export default {
  page: {
    title: "주요설비현황",
    meta: [
      {
        name: "description",
        content: appConfig.description,
      },
    ],
  },
  components: {
    Layout,
    PageHeader,
    MainFacilityStatusItem,
    MainIotStatusItem,
    SensorEventTimeLine,
    FacilityStatusChart,
    SensorDetails,
  },
  data() {
    return {
      monType: "설비", //"IoT"
      plantNum: 9, //10
      facilTurbin: facilTurbin,
      facilBoiler: facilBoiler,
      facilStorage: facilStorage,

      iotTurbin: iotTurbin,
      iotBoiler: iotBoiler,
      iotStorage: iotStorage,

      columnChart: columnChart,
      title: "IoT감지기현황",
      items: [
        {
          text: "종합지표현황",
          href: "/",
        },
        {
          text: "주요설비현황",
          active: true,
        },
      ],
      toggleMode: "",
      selectedItemIndex: -1,
      selectedItem: "",
      isFacil: true,
      activeTabId:9,
      defaultPlantNum: 9
    };
  },
  computed: {
    isTurbinVisible() {
      return this.toggleMode.includes("turbin") || this.toggleMode === "";
    },
    isBoilerVisible() {
      return this.toggleMode.includes("boiler") || this.toggleMode === "";
    },
    isStorageVisible() {
      return this.toggleMode.includes("storage") || this.toggleMode === "";
    },
    isSensorDetailVisible() {
      return this.toggleMode.includes("sensor");
    },
    isChartVisible() {
      return this.toggleMode !== "" && !this.toggleMode.includes("sensor");
    },
    chartTitle() {
        if (this.toggleMode === "turbin") {
            return "터빈"
        } else if (this.toggleMode === "boiler") {
            return "보일러"
        } else if (this.toggleMode === "storage") {
            return "옥내저탄장"
        } else {
            return ""
        }
    }
  },
  methods: {
    toggleClass(tagName, selId = -1, selItem = "") {
      console.log("toggleClass:" + tagName + "  :" + this.toggleMode);
      if (tagName.includes("sensor")) {
        //this.toggleMode = (this.toggleMode.includes("sensor"))?tagName.replace('sensor-',''):tagName
        if (tagName === this.toggleMode && this.selectedItemIndex !== selId) {
            //
        } else {
            this.toggleMode = (this.toggleMode != "" )? "" : tagName;
        }
      } else if (tagName === "turbin") {
        this.toggleMode = this.toggleMode != "" ? "" : tagName;
      } else if (tagName === "boiler") {
        this.toggleMode = this.toggleMode === "boiler" ? "" : tagName;
      } else if (tagName === "storage") {
        this.toggleMode = this.toggleMode === "storage" ? "" : tagName;
      }
      this.selectedItemIndex = selId;
      this.selectedItem = selItem;

      console.log("toggleMode :" + this.toggleMode);
      // } else if (tagName.includes("turbin")) {
      //     this.toggleMode = (this.toggleMode.includes("turbin"))?"":tagName
      // } else if (tagName === "boiler") {
      //     this.toggleMode = (this.toggleMode.includes("boiler"))?"":tagName
      // } else if (tagName === "storage") {
      //     this.toggleMode = (this.toggleMode.includes("storage"))?"":tagName
      // }
      //toggleClass
    },
    switchTab(tabId) {
      console.log("switchTab")
      if (this.activeTabId==tabId) return
      
      let tabFacility = document.getElementById('tabFacility')
      tabFacility.parentNode.removeChild(tabFacility)
      document.getElementById((tabId==9)?'tabPlant9':'tabPlant10').appendChild(tabFacility)
      this.activeTabId=tabId
    }
  },
  mounted() {
      const path = this.$route.path
      this.monType = this.$route.params.filter
      this.plantNum = Number(this.$route.params.plantnum)
      console.log(`path ${path} monType ${this.monType} plantNum ${this.plantNum}`)
      this.isFacil=(path.indexOf("facil")>=0) 
      if (!this.isFacil) {
        this.facilTurbin=this.iotTurbin
        this.facilBoiler=this.iotBoiler
        this.facilStorage=this.iotStorage
        this.title = "IoT감지기현황"
      } else {
        this.title = "주요설비현황"
      }
      if (this.defaultPlantNum !== this.plantNum) {
        this.switchTab(this.plantNum)
      }
  },
};
</script>
<template>
  <Layout>
    <PageHeader :title="title" :items="items" />
    <div class="row">
      <div class="col-xl-12 ">
        <b-tabs content-class="mt-0" nav-class="nav-tabs-custom" class="border-sensor-main  nav-justified">
          <b-tab title="9호기" :active="(activeTabId===9)"  @click="switchTab(9)" class="border-sensor-main" id="tabPlant9">
            <div role="tabpanel" class="tab-pane fade show active" id="tabFacility">
              <div class="row">
                <div class="col-xl-3" v-if="isTurbinVisible">
                  <div class="card-box border border-warning card-sensor">
                    <h4 class="header-title mb-2">
                      {{ facilTurbin.facility }}
                      <div class="float-right">
                      <a href="#" @click="toggleClass('turbin')" class="btn btn-icon">
                          <i class="mdi mdi-chart-line"></i>
                      </a>
                      </div>
                    </h4>
                    <div class="separator"></div>
                    <template v-if="isFacil">
                      <div class="sensor-widget"
                          v-for="(item, index) in facilTurbin.items"
                          :key="index"
                          @click="toggleClass('sensor-turbin', index, item.item)"
                      >
                        <main-facility-status-item
                        :id="facilTurbin.id + '_' + index"
                        :title="item.item"
                        :needNarrow="false"
                        :items="item.checkPoints"
                        :selected="
                            toggleMode === 'sensor-turbin' &&
                            selectedItemIndex == index
                        "
                        :lastItem="facilTurbin.items.length == index + 1"
                        :alarmLevel="item.alarmLevel ? item.alarmLevel : 'muted'"
                        ></main-facility-status-item>
                      </div>
                    </template>
                    <template v-else>
                        <main-iot-status-item
                        :tag="facilTurbin.id"
                        :needNarrow="false"
                        :iotData="facilTurbin"
                        @onToggleClass="toggleClass"
                        :selectedItemIndex="selectedItemIndex"
                        :selectedItem="selectedItem"
                        ></main-iot-status-item>
                    </template>
                  </div>
                </div>
                <!--보일러-->
                <div class="col-xl-3" v-if="isBoilerVisible">
                  <div class="card-box border border-warning card-sensor">
                    <h4 class="header-title mb-2">
                        {{ facilBoiler.facility }}
                        <div class="float-right">
                        <a
                            href="#"
                            @click="toggleClass('boiler')"
                            class="btn btn-icon"
                        >
                            <i class="mdi mdi-chart-line"></i>
                        </a>
                        </div>
                    </h4>
                    <div class="separator"></div>
                    <template v-if="isFacil">
                      <div
                          class="sensor-widget"
                          v-for="(item, index) in facilBoiler.items"
                          :key="index"
                          @click="toggleClass('sensor-boiler', index, item.item)"
                      >
                        <main-facility-status-item
                        :id="facilBoiler.id + '_' + index"
                        :title="item.item"
                        :needNarrow="true"
                        :items="item.checkPoints"
                        :selected="
                            toggleMode === 'sensor-boiler' &&
                            selectedItemIndex == index
                        "
                        :lastItem="facilBoiler.items.length == index + 1"
                        :alarmLevel="item.alarmLevel ? item.alarmLevel : 'muted'"
                        ></main-facility-status-item>
                      </div>
                    </template>
                    <template v-else>
                        <main-iot-status-item
                        :tag="facilBoiler.id"
                        :needNarrow="false"
                        :iotData="facilBoiler"
                        @onToggleClass="toggleClass"
                        :selectedItemIndex="selectedItemIndex"
                        :selectedItem="selectedItem"
                        ></main-iot-status-item>
                    </template>
                  </div>
                </div>
                <!-- 보일러 end -->
                <!-- 옥내저탄장 -->
                <div class="col-xl-3" v-if="isStorageVisible">
                  <div class="card-box border border-warning card-sensor">
                    <h4 class="header-title mb-2">
                        {{ facilStorage.facility }}
                        <div class="float-right">
                        <a
                            href="#"
                            @click="toggleClass('storage')"
                            class="btn btn-icon"
                        >
                            <i class="mdi mdi-chart-line"></i>
                        </a>
                        </div>
                    </h4>
                    <div class="separator"></div>
                    <template v-if="isFacil">
                      <div
                        class="sensor-widget"
                        v-for="(item, index) in facilStorage.items"
                        :key="index"
                        @click="toggleClass('sensor-storage', index, item.item)"
                      >
                        <main-facility-status-item
                        :id="facilStorage.id + '_' + index"
                        :title="item.item"
                        :needNarrow="false"
                        :items="item.checkPoints"
                        :selected="
                            toggleMode === 'sensor-storage' &&
                            selectedItemIndex == index
                        "
                        :lastItem="facilStorage.items.length == index + 1"
                        :alarmLevel="item.alarmLevel ? item.alarmLevel : 'muted'"
                        ></main-facility-status-item>
                      </div>
                    </template>
                    <template v-else>
                      <main-iot-status-item
                      :tag="facilStorage.id"
                      :needNarrow="false"
                      :iotData="facilStorage"
                      @onToggleClass="toggleClass"
                      :selectedItemIndex="selectedItemIndex"
                      :selectedItem="selectedItem"
                      ></main-iot-status-item>
                    </template>
                  </div>
                </div>
                <!-- 옥내저탄장 end -->

                <!-- 상세 차트 -->
                <div
                    class="col-lg-6 grid-margin stretch-card facility-toggle"
                    v-if="isChartVisible"
                >
                    <div class="card">
                    <div class="card-body border border-warning">
                        <h4 class="card-title">{{ chartTitle }} 위험율 추이</h4>
                        <facility-status-chart></facility-status-chart>
                    </div>
                    </div>
                    <div class="card">
                    <div class="card-body border border-warning">
                        <h4 class="card-title">{{ chartTitle }} 설비별 위험율 현황</h4>
                        <apexchart
                        class="apex-charts"
                        height="250"
                        type="bar"
                        dir="ltr"
                        :series="columnChart.series"
                        :options="columnChart.chartOptions"
                        ></apexchart>
                    </div>
                    </div>
                </div>
                <!-- 상세 차트 end-->
                <!-- 상세목록 -->
                <div
                    class="col-lg-6 grid-margin stretch-card facility-toggle"
                    v-if="isSensorDetailVisible"
                >
                    <sensor-details :title="selectedItem"></sensor-details>
                </div>
                <!-- 상세목록 end -->

                <div class="col-xl-3">
                    <sensor-event-time-line
                      :plantNo=plantNum
                    ></sensor-event-time-line>
                </div>
                <!-- end col-->
              </div>
            </div>
          </b-tab>
          <b-tab title="10호기" :active="(activeTabId===10)" @click="switchTab(10)" class="border-sensor-main" id="tabPlant10">
          </b-tab>
        </b-tabs>
      </div>
    </div>
  </Layout>
</template>

<style></style>
